import Black from "../../components/qy/Black";
import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
export default {
  
	data() {
		return {
		  list:[1,2,3,4,5,6],
		  geze:false
		};
	},
	computed: {
		
	},
	components: {
		Black,uniIcon
	},
	methods: {
		show(){
		  this.geze=true
		},
		close(){
		  this.geze=false
		}
	 },
		onPullDownRefresh() {
			//this._load()
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 2000);
		}
};
